import { CButton } from "@coreui/react";
import axios from "axios";
import React, { useState } from "react";
import { useEffect } from "react";
import { Link } from "react-router-dom";
import Tabs from "../Tabs";
import FollowUpCalls from "./FollowUpCalls";
import MemberCalls from "./MemberCalls";
import SubmitAppointment from "./SubmitAppointment";
const url = 'https://yog-seven.vercel.app'

const CallHistary = ({ clientId }) => {

  let user = JSON.parse(localStorage.getItem('user-info'))
  const token = user.token;
  const username = user.user.username;
  const centerCode = user.user.centerCode;
  const [result, setResult] = useState([])
  useEffect(() => {
    getProspect(clientId)
  }, [])

  function getProspect(id) {
    axios.get(`${url}/prospect/all`, {
      headers: {
        'Authorization': `Bearer ${token}`
      }
    })
      .then((res) => {
        setResult(res.data.filter((list) => list.EnquiryID === id))
      })
      .catch((error) => {
        console.error(error)
      })
  }
  const data = [
    {
      title: "Follow Calls",
      compo: <FollowUpCalls clientData={result} />,
      bgcolor: "#58D68D",
    },
    {
      title: "Member Calls",
      compo: <MemberCalls />,
      bgcolor: "#F39C12",
    },
  ];
  const [openCal, setCal] = useState(false)
  return (
    <>
      <div className="d-flex justify-content-between mt-3 ml-2 mr-2 mb-2">
        <div className="title ml-3"><b>Call History</b></div>
        <div>
          <CButton className="btn filter-btn1 " style={{ marginRight: "5px" }}>
            Inter branch transfer
          </CButton>
          <CButton className="btn filter-btn1 " style={{ marginRight: "5px" }}>
            Print Profile
          </CButton>
          <CButton className="btn filter-btn1 " style={{ marginRight: "5px" }}>

            <Link to={`/forms/invoice/${clientId}`} style={{ color: 'white', textDecoration: 'none' }}>New Invoice</Link>
          </CButton>
          <CButton className="btn filter-btn1 " style={{ marginRight: "5px" }}>
            New Call
          </CButton>
          <CButton className="btn filter-btn1 " onClick={() => setCal(!openCal)}>New Appointment</CButton>
        </div>
      </div>
      {openCal &&
        <SubmitAppointment id={id} />
      }
      <div className="mt-3">
        <Tabs data={data} />
      </div>
    </>
  );
};

export default CallHistary;
