import { CButton, CCol, CFormInput, CFormSelect, CRow } from "@coreui/react";
import axios from "axios";
import React, { useEffect } from "react";
import { useState } from "react";
import { FiAlertCircle } from "react-icons/fi";
import { Link } from "react-router-dom";
import profile from 'src/assets/images/avatars/profile_icon.png'
import Table from "../Table";
import SubmitAppointment from "./SubmitAppointment";
const url = 'https://yog-seven.vercel.app'

const Attendance = ({ clientId }) => {

  let user = JSON.parse(localStorage.getItem('user-info'))
  const token = user.token;
  const username = user.user.username;
  const centerCode = user.user.centerCode;

  const [attendanceDetails, setAttendanceDetails] = useState({
    center: '',
    attendanceId: '',
    date: '',
    time: '',
  })

  const [clientDetails, setClientDetails] = useState({
    name: '',
    number: '',
    service: '',
  })

  const [result, setResult] = useState([])
  const inputEvent = (e) => {
    const { name, value } = e.target;
    setAttendanceDetails((olddata) => {
      return {
        ...olddata,
        [name]: value
      }
    })
  }

  const inputData = (name, value) => {
    setClientDetails((olddata) => {
      return {
        ...olddata,
        [name]: value
      }
    })
  }

  useEffect(() => {
    getAttendance()
    searchSingle(clientId)
  }, [])

  function searchSingle(id) {
    const headers = {
      'Authorization': `Bearer ${token}`,
      'My-Custom-Header': 'foobar'
    };
    axios.get(`${url}/memberForm/${id}`, { headers },
    ).then((resp) => {
      console.log(resp.data);
      inputData('name', resp.data.Fullname)
      inputData('number', resp.data.ContactNumber)
      inputData('service', resp.data.serviceName)
      setAttendanceDetails((oldData) => {
        return {
          ...oldData,
          ["attendanceId"]: resp.data.AttendanceID
        }
      })

    }).catch((err) => {
      console.log(err);
    })
  }

  const getAttendance = () => {
    axios.get(`${url}/clientAttendance/all`, {
      headers: {
        'Authorization': `Bearer ${token}`
      }
    })
      .then((res) => {
        setResult(res.data.reverse().filter((list) => list.clientId === clientId))
        console.log(res.data);
      })
      .catch((error) => {
        console.error(error)
      })
  }

  const submitAttendance = () => {
    const data = {
      username: username,
      clientId: clientId,
      attentanceId: attendanceDetails.attendanceId,
      ClientName: clientDetails.name,
      ClientMobile: clientDetails.number,
      ServiceName: clientDetails.service,
      Group: '',
      PT: '',
      centerId: attendanceDetails.center,
      Branch: attendanceDetails.center,
      checkDate: attendanceDetails.date,
      checkIn: attendanceDetails.time,
    }
    axios.post(`${url}/clientAttendance/create`, data, {
      headers: {
        'Authorization': `Bearer ${token}`
      }
    })
      .then((res) => {
        alert('Checkin successfully');
      })
      .catch((error) => {
        console.error(error)
      })
  }

  const column = [
    /* 
    value: keyword for normal value passing
    btn: keyword for button
    btn1 to btn4: keyword for component passing
    lebel: keyword for anchor tag
    Note: please don't pass empty values or perameters
    */
    { heading: "S.No", uni: '' },
    { heading: "Date", date: "checkDate" },
    { heading: "Name", value: "ClientName" },
    { heading: "Mobile", value: "ClientMobile" },
    { heading: "Service", value: "ServiceName" },
    { heading: "Branch", value: "Branch" },
    { heading: "CheckIn", value: "checkIn" },
    { heading: "CheckOut", value: "checkOut" },
    { heading: "Group", value: "Group" },
    { heading: "PT", value: "PT" },
    { heading: "Condcuted By", value: "Condcuted" },
    { heading: "Alert", btn: <FiAlertCircle size='16px' /> },
  ];

  const [openCal, setCal] = useState(false)
  return (
    <>
      <div className="d-flex justify-content-between mt-3 ml-2 mr-2 mb-2">
        <div className="title ml-3"><b>Attendance</b></div>
        <div>
          <CButton className="btn filter-btn1 " style={{ marginRight: "5px" }}>
            Inter branch transfer
          </CButton>
          <CButton className="btn filter-btn1 " style={{ marginRight: "5px" }}>
            Print Profile
          </CButton>
          <CButton className="btn filter-btn1 " style={{ marginRight: "5px" }}>

            <Link to={`/forms/invoice/${clientId}`} style={{ color: 'white', textDecoration: 'none' }}>New Invoice</Link>
          </CButton>
          <CButton className="btn filter-btn1 " style={{ marginRight: "5px" }}>
            New Call
          </CButton>
          <CButton className="btn filter-btn1 " onClick={() => setCal(!openCal)}>New Appointment</CButton>
        </div>
      </div>
      {openCal &&
        <SubmitAppointment id={clientId} />
      }
      <div className="d-flex justify-content-between mt-5">
        <CRow>
          <CCol lg={3} sm={6}>
            <div className="d-flex left ml-3" style={{ gap: "30px" }}>
              <img
                height="200px"
                style={{ borderRadius: "5px" }}
                src={profile}
                alt="Profile"
              />
            </div>
          </CCol>
          <CCol lg={8} sm={12}>
            <CRow>
              <CCol className="mt-2" lg={12} sm={12}>
                <div className="d-flex left">
                  Scan Membership Card Or Enter Attendance Id
                </div>
              </CCol>
              <CCol className="mt-2" lg={3} sm={3}>
                <div className="d-flex left" style={{ gap: "30px" }}>
                  <div className="mr-3 ">Check in Type:</div>
                </div>
              </CCol>
              <CCol className="mt-2" lg={9} sm={9}>
                <div className="d-flex " style={{ width: '215px', gap: "30px" }}>
                  <CFormSelect
                    value={attendanceDetails.center}
                    name="center"
                    onChange={inputEvent}
                    options={[
                      "Select",
                      { label: "Current Branch", value: centerCode },
                      { label: 'Multiple Centers', value: 'Multiple Centers' }
                    ]}
                  />
                </div>
              </CCol>
              <CCol className="mt-2" lg={3} sm={3}>
                <div className="d-flex left">Attendance Id</div>
              </CCol>
              <CCol className="mt-2" lg={9} sm={9}>
                <div className="d-flex left" style={{ width: '215px' }}>
                  <CFormInput
                    type="text"
                    value={attendanceDetails.attendanceId}
                    name="attendanceId"
                    onChange={inputEvent}
                  />
                </div>
              </CCol>
              <CCol className="mt-2" lg={3} sm={3}>
                <div className="d-flex left">Date</div>
              </CCol>
              <CCol className="mt-2" lg={9} sm={9}>
                <div className="d-flex left" style={{ width: '215px' }}>
                  <CFormInput
                    type="date"
                    value={attendanceDetails.date}
                    name="date"
                    onChange={inputEvent}
                  />
                </div>
              </CCol>
              <CCol className="mt-2" lg={3} sm={3}>
                <div className="d-flex left">Time</div>
              </CCol>
              <CCol className="mt-2" lg={5} sm={5}>
                <div className="d-flex left " style={{ width: '215px' }}>
                  <CFormInput
                    type="time"
                    value={attendanceDetails.time}
                    name="time"
                    onChange={inputEvent}
                  />
                </div>
              </CCol>
              <CCol className="ml-2 mt-2" lg={3} sm={3}>
                <div className="d-flex left ml-2">
                  <CButton className="ml-2 mr-2" onClick={submitAttendance} > Check In</CButton>
                </div>
              </CCol>
            </CRow>
          </CCol>
        </CRow>
      </div>
      <div>
        <div className="d-flex ml-3 mt-3"><b>Attendance Details</b></div>
        <Table data={result} column={column} />
      </div>
    </>
  );
};

export default Attendance;
